import Vue from 'vue'
import Router from 'vue-router'
const home=()=>import("@/views/tabBar/home/home")
const category=()=>import("@/views/tabBar/category/category")
const cart=()=>import("@/views/tabBar/cart/cart")
const user=()=>import("@/views/tabBar/user/user")
const goodsList=()=>import("@/views/goods/goodsList")
const goods=()=>import("@/views/goods/goods")
const comments=()=>import("@/views/goods/comments")
const confirmOrder=()=>import("@/views/goods/confirmOrder")

Vue.use(Router)

export default new Router({
  scrollBehavior (to, from, savedPosition) {
      if(savedPosition){
        return savedPosition
      }
    return { x: 0, y: 0 }
    // 导致页面返回时没有滚动到离开时的位置是因为页面是内容是通过网络请求之后再渲染的。
  },
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/category',
      name: 'category',
      component: category
    },
    {
      path: '/cart',
      name: 'cart',
      component: cart
    },
    {
      path: '/user',
      name: 'user',
      component: user
    },
    {
      path: '/goodsList',
      name: 'goodsList',
      component: goodsList,
      meta:{
        notabBar:true
      }
    },
    {
      path: '/goods',
      name: 'goods',
      component: goods,
      meta:{
        notabBar:true
      }
    },
    {
      path: '/comments',
      name: 'comments',
      component: comments,
      meta:{
        notabBar:true
      }
    },
    {
      path: '/confirmOrder',
      name: 'confirmOrder',
      component: confirmOrder,
      meta:{
        notabBar:true
      }
    },
    {
      path:'*',
      redirect:'/'
    }
  ]
})




