<template>
  <div id="app">
  	<transition name="router-fade" mode="out-in" >
      <keep-alive>
    	 <router-view/>
      </keep-alive>
    </transition>
    <van-tabbar v-model="active" route active-color="rgb(240, 108, 122)" v-show='!$route.meta.notabBar'>
	    <van-tabbar-item to='/'>
		  	<span>首页</span>
		  	 <img
	          slot="icon"
	          slot-scope="props"
	          :src="props.active ? homeIco.active : homeIco.normal"
	        >
	    </van-tabbar-item>
	    <van-tabbar-item to='/category'>
		  	<span>分类</span>
		  	 <img
	          slot="icon"
	          slot-scope="props"
	          :src="props.active ? categoryIco.active : categoryIco.normal"
	        >
	    </van-tabbar-item>
	    <van-tabbar-item to='/cart'>
	  	  <span>购物车</span>
	  	  <img
          slot="icon"
          slot-scope="props"
          :src="props.active ? cartIco.active : cartIco.normal"
        >
	    </van-tabbar-item>
	    <van-tabbar-item to='/user'>
        <span>我的</span>
         <img
            slot="icon"
            slot-scope="props"
            :src="props.active ? userIco.active : userIco.normal"
          >
      </van-tabbar-item>
	  </van-tabbar>
  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
  	return{
  		active:0,
  		homeIco:{
  			normal: require('@/assets/images/taBar-img/home.png'),
            active:require('@/assets/images/taBar-img/home-on.png')
  		},
  		categoryIco:{
  			normal: require('@/assets/images/taBar-img/category.png'),
            active:require('@/assets/images/taBar-img/category-on.png')
  		},
  		cartIco:{
  			normal: require('@/assets/images/taBar-img/cart.png'),
            active:require('@/assets/images/taBar-img/cart-on.png')
  		},
  		userIco:{
  			normal: require('@/assets/images/taBar-img/user.png'),
            active:require('@/assets/images/taBar-img/user-on.png')
  		}
  	}
  }
}
</script>

<style lang='stylus'>
  html
    font-size 13.333333vw
    height 100%
  body
    height 100%
    background:#fff
  #app
    height:100%;
    .van-tabbar--fixed 
      z-index:101 !important;
      border-top 1px solid rgba(0, 0, 0, 0.33)
      padding-top .12rem
      box-sizing: border-box;
      height:1.1rem;
      bottom:-1px;
  .router-fade-enter-active, .router-fade-leave-active 
    transition opacity .18s
  
  .router-fade-enter 
  .router-fade-leave-active 
    opacity 0
    
  
  .van-tabbar-item 
  	font-size .27rem
  	margin-bottom .15rem
    
  .van-tabbar-item__icon 
    img 
  	  height: .56rem !important;
    	width: .56rem;
  .van-toast-1 
    width:55vw !important;
    font-size:.3rem !important;
</style>



